import React, { PureComponent } from 'react';
import classNames from 'classnames';
import './index.css';
export default class Footer extends PureComponent {
    render() {
        const { style, className, prefixCls, noBorder, children } = this.props;
        const footerClassName = classNames(prefixCls, className, {
            [`${prefixCls}-noborder`]: noBorder
        });
        return (<div style={style} className={footerClassName}>
                {children && children}
            </div>);
    }
}
Footer.defaultProps = {
    prefixCls: 'zzc-card-footer',
    className: '',
    children: null,
    noBorder: false,
    style: {}
};



