import React from 'react';
export default class TabsPane extends React.PureComponent {
    render() {
        const { prefixCls, children, itemKey } = this.props;
        return (<div key={itemKey} className={`${prefixCls}-content-item`}>
                {children}
            </div>);
    }
}



