import React, { PureComponent } from 'react';
import classNames from 'classnames';
import Dialog from '../dialog';
import Button from '../button';
import './index.css';
export default class Modal extends PureComponent {
    constructor() {
        super(...arguments);
        this.state = {
            visible: this.props.visible
        };
    }
    componentWillReceiveProps(newProps) {
        if (this.state.visible !== newProps.visible) {
            this.setState({
                visible: newProps.visible
            });
        }
    }
    createAlertFooter(closable, buttons) {
        if (closable) {
            return (<Button noBorder full onClick={() => {
                this.close();
            }}>
                    关闭
                </Button>);
        }
        else if (buttons && buttons.length > 0) {
            return (buttons.map((item, key) => (<div key={`${this.props.prefixCls}-btn-${new Date().getTime()}-${key}`} className={classNames(`${this.props.prefixCls}-btn`)}>
                        {this.createBtn(item)}
                    </div>)));
        }
        return null;
    }
    createBtn(btn) {
        const onPress = btn.onPress ? btn.onPress : () => { };
        return React.cloneElement(<Button noBorder full onClick={(event) => {
            event.stopPropagation();
            const res = onPress();
            if (res && res.then) {
                res.then(() => {
                    this.close();
                }).catch(() => { });
            }
            else {
                this.close();
            }
        }}>
            {btn.text}
        </Button>, Object.assign({}, btn.props));
    }
    close() {
        const { transitionName, maskTransitionName, closeCallback } = this.props;
        // 当没有提供动画，无法触发动画事件触发外部传入的closeCallback，所以当没有动画则主动触发外部closeCallback
        if (transitionName === '' && maskTransitionName === '') {
            closeCallback && closeCallback();
        }
        else {
            this.setState({
                visible: false
            });
        }
    }
    createTitle(title) {
        const { prefixCls } = this.props;
        if (title && title !== '') {
            return (<div className={classNames(`${prefixCls}-header`)}>
                    {title}
                </div>);
        }
        return null;
    }
    createFooter() {
        const { prefixCls, buttons, closable } = this.props;
        return (<div className={classNames(`${prefixCls}-footer`)}>
                {this.createAlertFooter(closable, buttons)}
            </div>);
    }
    render() {
        const { children, maskClose, transitionName, maskTransitionName, prefixCls, closeCallback, className, style, maskStyle, title, visible, transparent } = this.props;
        if (visible) {
            return (<Dialog transparent={transparent} maskTransitionName={maskTransitionName} visible={this.state.visible} style={style} maskStyle={maskStyle} maskClose={maskClose} transitionName={transitionName} boxClassName={classNames(`${prefixCls}-box`, className)} closeCallback={closeCallback} title={this.createTitle(title)} footer={this.createFooter()}>
                    <div className={classNames(`${prefixCls}-body`)}>
                        {children}
                    </div>
                </Dialog>);
        }
        return null;
    }
}
Modal.defaultProps = {
    prefixCls: 'zzc-modal',
    maskTransitionName: 'zzc-fade',
    style: {},
    maskStyle: {},
    className: '',
    closable: false,
    closeCallback() { },
    visible: false,
    title: '',
    transitionName: 'zzc-zoom',
    buttons: [],
    maskClose: false,
    transparent: false
};



