export function setLayoutScroll(layout, offset, unit, isVertical) {
    const value = isVertical ? `0px, ${offset}${unit}, 0px` : `${offset}${unit}, 0px, 0px`;
    const translate3d = `translate3d(${value})`;
    const style = layout.style;
    setTransfrom(style, translate3d);
}
export function getTabItemSize(maxTabLength) {
    return (100 / maxTabLength).toFixed(4);
}
export function getTranslate3dStyle(value, direction) {
    const offset = direction === 'vertical' ? `0px, ${value}, 1px` : `${value}, 0px, 1px`;
    const translate3d = `translate3d(${offset})`;
    return {
        transform: translate3d,
        WebkitTransform: translate3d,
        MozTransform: translate3d
    };
}
function setTransfrom(style, value) {
    style.transform = value;
    style.WebkitTransform = value;
    style.MozTransform = value;
}



