import React from 'react';
export default function TabsItem(props) {
    const { itemKey, className, clickEvent, style, item } = props;
    return (<div key={itemKey} className={className} style={style} onClick={() => { clickEvent(); }}>
            {React.isValidElement(item) ? item : item.title}
        </div>);
}



